.nav {
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px 120px;
    color: #fff;
  }
  
  .nav_logo {
    font-family: Outfit;
    font-size: 42px;
    font-weight: 500;
  }
  
  .nav_menu {
    display: flex;
    align-items: center;
    list-style: none;
    font-size: 18px;
    gap: 90px;
  }
  
  .nav_contact {
    border-radius: 50px;
    padding: 10px 38px;
    background: #fff;
    color: #262626;

  }
  